import React from 'react';
import productdata from "../../../data/details.json";

const ProductDescription = () =>{
  return (
    <div>
      <p key={productdata.productDetail.productName} style={{padding: "5px"}}>{productdata.productDetail.productName}</p>
      <p key={productdata.productDetail.productDescription} style={{padding: "5px"}}>{productdata.productDetail.productDescription}</p>
    </div>
  )
}

export default ProductDescription;
