import React from "react";
import "./Modal.css"

const Modal = (props) =>{
  return (
    <div
    className="modalcomponent"
    style={{
      display: props.show ? "block" : "none",
      transform: props.show ? "none" : "translate(-50%,-50%)"
    }}>
      <p className="modalpara">My Orders</p>
      <p className="modalpara">Profile</p>
      <p className="modalpara">Signout</p>
    </div>
  )
}

export default Modal;
